@import url('https://fonts.googleapis.com/css?family=Lobster|Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 1100px;
  margin: 0 auto;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  margin-top: 50px;
}
h1 {
  font-size: 50px;
  font-family: 'Lobster', cursive;
  margin-bottom: 20px;
}
.App form {
  display: inline-block;
  border-radius: 20px;
  box-shadow: 0px 0px 19px 1px rgba(0,0,0,0.39);
  margin-bottom: 30px;
}
.App input {
  line-height: 40px;
  border-radius: 20px 0 0 20px;
  /* border: 1px solid rgb(231, 231, 231); */
  border: none;
  width: 400px;
  padding: 0 20px;
}
.App input:focus {
  outline: none;
}
.App button {
  line-height: 40px;
  border: none;
  background-color: #F6F6F6;
  padding: 0 20px;
  border-radius: 0 20px 20px 0;
  cursor: pointer;
}
.App button:focus {
  outline: none;
}
.searchResultDiv {
  width: 800px;
  margin: 0 auto;
  text-align: left;
  margin-bottom: 20px;
}
.searchResultDiv h3 {
  color: #1A0DAB;
}
.searchResultDiv h3 a {
  display: inline-block;
  line-height: 16px;
  color: #1A0DAB;
  text-decoration: none;
}
.searchResultDiv h3 a:hover {
  /* text-decoration: underline; */
  border-bottom: 3px solid;
}
.searchResultDiv a:visited {
  color: #1A0DAB;
}
.searchResultDiv .link a {
  text-decoration: none;
  color: #287E44;
}
.searchResultDiv .link a:hover {
  text-decoration: none;
}
.searchResultDiv .searchmatch {
  font-weight: bold;
}
.searchResultDiv .description {
  color: #414141
}